body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

/* You can add global styles to this file, and also import other style files */
::-moz-selection {
  background: rgba(33, 68, 113, 0.4); }
::selection {
  background: rgba(33, 68, 113, 0.4); }

.mfp-img {
  max-height: 80vh !important; }


#about {
  min-height: 100vh; }

.about-section {
  overflow: hidden;
  background-color: #F9F9F9; }

.about-section img {
  position: absolute; }
  @media only screen and (max-width: 991px) {
    .about-section img {
      position: inherit;
      max-height: 80vh;
      object-fit: cover; } }

.about-me h2 {
  padding: 0px 50px; }

.about-me p {
  padding: 50px;
  font-size: 15px; }


.home-section-background {
  position: relative;
  height: 100vh;
  background: url(/static/media/bg.4fd47abc.jpg);
  background-size: cover;
  z-index: 1; }

.embedded-video {
  width: 100%;
  position: relative;
  /* If you want text inside of it */
  padding-top: 56.25%;
  /* 16:9 Aspect Ratio */ }
  .embedded-video .ratio-keeper {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0; }
    .embedded-video .ratio-keeper iframe {
      width: 100%;
      height: 100%;
      border: 0; }
  .embedded-video .youtube {
    background-color: #000;
    position: relative;
    padding-top: 56.25%;
    overflow: hidden;
    cursor: pointer; }
    .embedded-video .youtube__wrapper {
      margin: 60px auto;
      padding: 0 20px; }
    @media only screen and (min-width: 768px) {
      .embedded-video .youtube img {
        cursor: pointer;
        width: 100%;
        opacity: 0.75;
        left: 0;
        right: 0;
        object-fit: cover;
        height: 100%; } }
    @media only screen and (max-width: 767px) {
      .embedded-video .youtube img {
        cursor: pointer;
        position: absolute;
        width: 100%;
        top: -16.82%;
        opacity: 0.75;
        left: 0;
        right: 0; } }
    .embedded-video .youtube:hover .play-button {
      opacity: 1;
      background: red; }
    .embedded-video .youtube .play-button {
      position: absolute;
      width: 72px;
      height: 54px;
      top: 50%;
      left: 50%;
      -webkit-transform: translate3d(-50%, -50%, 0);
              transform: translate3d(-50%, -50%, 0);
      z-index: 1;
      opacity: 0.8;
      background: #202020;
      box-shadow: 0 0 30px rgba(0, 0, 0, 0.6);
      border-radius: 50% / 10%;
      text-align: center;
      -webkit-transition: all 150ms ease-out;
      transition: all 150ms ease-out; }
      .embedded-video .youtube .play-button::before {
        background: inherit;
        border-radius: 5% / 50%;
        bottom: 9%;
        content: "";
        left: -5%;
        position: absolute;
        right: -5%;
        top: 9%; }
      .embedded-video .youtube .play-button::after {
        border-style: solid;
        border-width: 1em 0 1em 1.732em;
        border-color: transparent transparent transparent rgba(255, 255, 255, 0.75);
        content: " ";
        font-size: 0.8em;
        margin: -1em 0 0 -0.75em;
        top: 50%;
        position: absolute; }
    .embedded-video .youtube iframe {
      position: absolute;
      height: 100%;
      width: 100%;
      top: 0;
      left: 0; }

@media only screen and (min-width: 768px) {
  .portfolio-inner .item a img, .portfolio-inner .youtube, .portfolio-inner .embedded-video {
    height: 200px;
    padding: 0;
    object-fit: cover; } }


